import React, { useState } from 'react';
import { useParams } from 'react-router-dom';
import {
  Container,
  Grid,
  Typography,
  Box,
  Button,
  Rating,
  Chip,
  Tabs,
  Tab,
  Divider,
  IconButton,
  ImageList,
  ImageListItem,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
} from '@mui/material';
import {
  FavoriteBorder,
  Favorite,
  LocalShipping,
  Security,
  Assignment,
  Star,
} from '@mui/icons-material';
import { products } from '../data/products';

const ProductDetails = () => {
  const { id } = useParams();
  const [selectedTab, setSelectedTab] = useState(0);
  const [selectedImage, setSelectedImage] = useState(0);
  const [isFavorite, setIsFavorite] = useState(false);
  const [selectedSize, setSelectedSize] = useState('');
  const [selectedColor, setSelectedColor] = useState('');

  const product = products.find(p => p.id === parseInt(id)) || products[0];

  const handleTabChange = (event, newValue) => {
    setSelectedTab(newValue);
  };

  return (
    <Container maxWidth="lg" sx={{ py: 6 }}>
      <Grid container spacing={6}>
        {/* Product Images */}
        <Grid item xs={12} md={7}>
          <Box sx={{ position: 'relative' }}>
            <IconButton
              onClick={() => setIsFavorite(!isFavorite)}
              sx={{
                position: 'absolute',
                right: 16,
                top: 16,
                bgcolor: 'white',
                '&:hover': { bgcolor: 'white' },
                zIndex: 1,
              }}
            >
              {isFavorite ? <Favorite color="primary" /> : <FavoriteBorder />}
            </IconButton>
            <Box
              component="img"
              src={product.images[selectedImage]}
              alt={product.name}
              sx={{
                width: '100%',
                height: 500,
                objectFit: 'cover',
                borderRadius: 2,
                mb: 2,
              }}
            />
            <ImageList
              sx={{ width: '100%', height: 100 }}
              cols={4}
              rowHeight={100}
              gap={8}
            >
              {product.images.map((img, index) => (
                <ImageListItem
                  key={index}
                  sx={{
                    cursor: 'pointer',
                    opacity: selectedImage === index ? 1 : 0.7,
                    transition: 'opacity 0.2s',
                    '&:hover': { opacity: 1 },
                  }}
                  onClick={() => setSelectedImage(index)}
                >
                  <img
                    src={img}
                    alt={`${product.name} ${index + 1}`}
                    style={{
                      height: '100%',
                      width: '100%',
                      objectFit: 'cover',
                      borderRadius: 1,
                    }}
                  />
                </ImageListItem>
              ))}
            </ImageList>
          </Box>
        </Grid>

        {/* Product Info */}
        <Grid item xs={12} md={5}>
          <Box sx={{ position: 'sticky', top: 100 }}>
            {product.bestseller && (
              <Chip
                icon={<Star sx={{ fontSize: 16 }} />}
                label="Bestseller"
                size="small"
                sx={{
                  bgcolor: '#FFD700',
                  color: '#000',
                  fontWeight: 600,
                  mb: 2,
                }}
              />
            )}
            
            <Typography variant="h4" component="h1" gutterBottom>
              {product.name}
            </Typography>

            <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
              <Rating value={product.rating} precision={0.1} readOnly />
              <Typography variant="body2" color="text.secondary" sx={{ ml: 1 }}>
                ({product.reviews} reviews)
              </Typography>
            </Box>

            <Typography variant="h4" color="primary" sx={{ mb: 3 }}>
              ${product.price}
            </Typography>

            <Typography variant="body1" color="text.secondary" paragraph>
              {product.description}
            </Typography>

            <Box sx={{ mb: 3 }}>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 2 }}>
                <LocalShipping color="action" />
                <Typography variant="body2" color="text.secondary">
                  {product.shipping}
                </Typography>
              </Box>
            </Box>

            {product.colors.length > 0 && (
              <FormControl fullWidth sx={{ mb: 2 }}>
                <InputLabel>Color</InputLabel>
                <Select
                  value={selectedColor}
                  onChange={(e) => setSelectedColor(e.target.value)}
                  label="Color"
                >
                  {product.colors.map((color) => (
                    <MenuItem key={color} value={color}>
                      {color}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            )}

            {product.sizes.length > 0 && (
              <FormControl fullWidth sx={{ mb: 3 }}>
                <InputLabel>Size</InputLabel>
                <Select
                  value={selectedSize}
                  onChange={(e) => setSelectedSize(e.target.value)}
                  label="Size"
                >
                  {product.sizes.map((size) => (
                    <MenuItem key={size} value={size}>
                      {size}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            )}

            <Button
              variant="contained"
              size="large"
              fullWidth
              sx={{ mb: 2, py: 1.5 }}
              disabled={!product.inStock}
            >
              {product.inStock ? 'Add to Cart' : 'Out of Stock'}
            </Button>

            <Divider sx={{ my: 4 }} />

            {/* Product Details Tabs */}
            <Box sx={{ width: '100%' }}>
              <Tabs
                value={selectedTab}
                onChange={handleTabChange}
                aria-label="product details tabs"
                sx={{ mb: 2 }}
              >
                <Tab label="Details" icon={<Assignment />} iconPosition="start" />
                <Tab label="Shipping" icon={<LocalShipping />} iconPosition="start" />
                <Tab label="Guarantee" icon={<Security />} iconPosition="start" />
              </Tabs>

              <Box hidden={selectedTab !== 0}>
                <Typography variant="subtitle1" gutterBottom>
                  Product Features
                </Typography>
                <Box component="ul" sx={{ pl: 2 }}>
                  {product.details.features.map((feature, index) => (
                    <Typography component="li" key={index} color="text.secondary" paragraph>
                      {feature}
                    </Typography>
                  ))}
                </Box>
                <Grid container spacing={2} sx={{ mt: 2 }}>
                  <Grid item xs={6}>
                    <Typography variant="subtitle2">Material</Typography>
                    <Typography color="text.secondary">{product.details.material}</Typography>
                  </Grid>
                  <Grid item xs={6}>
                    <Typography variant="subtitle2">Care</Typography>
                    <Typography color="text.secondary">{product.details.care}</Typography>
                  </Grid>
                </Grid>
              </Box>

              <Box hidden={selectedTab !== 1}>
                <Typography variant="body1" paragraph>
                  We offer the following shipping options:
                </Typography>
                <Box component="ul" sx={{ pl: 2 }}>
                  <Typography component="li" color="text.secondary" paragraph>
                    Standard Shipping (5-7 business days)
                  </Typography>
                  <Typography component="li" color="text.secondary" paragraph>
                    Express Shipping (2-3 business days)
                  </Typography>
                  <Typography component="li" color="text.secondary" paragraph>
                    Free shipping on orders over $50
                  </Typography>
                </Box>
              </Box>

              <Box hidden={selectedTab !== 2}>
                <Typography variant="body1" paragraph>
                  Our products come with:
                </Typography>
                <Box component="ul" sx={{ pl: 2 }}>
                  <Typography component="li" color="text.secondary" paragraph>
                    30-day money-back guarantee
                  </Typography>
                  <Typography component="li" color="text.secondary" paragraph>
                    1-year warranty against defects
                  </Typography>
                  <Typography component="li" color="text.secondary" paragraph>
                    Satisfaction guaranteed or full refund
                  </Typography>
                </Box>
              </Box>
            </Box>
          </Box>
        </Grid>
      </Grid>
    </Container>
  );
};

export default ProductDetails;
